.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.input {
  padding: 5px 20px;
  outline:0;
  border: 0;
}
#button:hover{
  background: #282c34;
  border: 0;
  color: #fff;
}
.App-link {
  color: #61dafb;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.todo_input{
  display: flex;
  justify-content: center;
}
.todo_input > button {
  margin-left: 20px;
}
.RecipeReviewCard{

  display: flex;
  justify-content: space-between;
  margin: 20px auto;
  width: 90%;
}
.MuiCard-root{
  background: darkcyan !important;
  color: #fff !important;
}